<template>
   <div class="max-w-4xl mx-auto max-h-screen px-2 z-0 bg-custoum p-2 rounded">
    <!-- <div class="message">{{ userdata }}</div> -->
       <form @submit.prevent="userLogin" >
            <div class="w-full grid grid-cols-12 gap-2">
                <div class="col-span-6">
                    <img src="../../../assets/join.gif"  />
                </div>
                <div class="col-span-6 flex flex-col mx-auto items-end justify-center gap-4">
                    <h2>{{ pageTitle }}</h2>
                    <div class="block w-full">
                        <Input
                            v-model="password"
                            class="w-full focus:outline-none text-center"
                            placeholder="Password"
                            autocomplete="off"
                        />
                    </div>
                    <!-- <div class="w-full flex justify-start items-center">
                        <label for="sms_input" class="flex items-center gap-2 text-md md:text-xl hover:underline">
                            <input id="sms_input" type="checkbox" v-model="sms" class="hidden w-5 h-5" />
                            I don't remember my password
                        </label>
                     </div> -->
                     <div class="flex justify-center">
                        <button type="submit" class="bg-brand text-white py-1.5 px-2 rounded-md flex items-center"> 
                            <spinner v-if="loading" />
                             Login 
                        </button>
                     </div>
                </div>
            </div>
       </form>
   </div>
</template>

<script>
import Input from '../../../components/input-new.vue'
import Spinner from '../../../components/spinner.vue'
// import ShowError from "../../../components/show-error.vue";
export default {
   components:{
       Input,
       Spinner,
   },
   data() {
    return {
        login: false,
        send: false,
        sms: false,
        name:this.$route.query.name ||'',
        phone: this.$route.query.phone || '',
        step:1,
        loading: false,
        pageTitle: "Join Page",
        password: '',
    }
  },
  computed:{
    searchUser() {
            return this.$store.state.join.user;
        },
    user_info(){
       return this.$route.params.name;
     },
    loginIn(){
        return  this.$store.state.login.isLoggedIn;
    }

  },

created(){
    // this.$store.dispatch('login/findUser');
},
watch: {
    '$store.state.join.user'(data){
        this.phone = data.phone,
        this.name = data.name
    }
},

   methods:{
        userLogin(){
            this.loading = true;
            this.formErrors = null;

            if(this.phone.length == 11){
                const response = this.$store.dispatch("login/findUser", {
                    data: { 
                        password: this.password,
                        phone: this.phone,
                        // login_type: "mobile_number",
                        // name: this.name
                    }
                }).then(  () => {
                    this.$router.push("appointments")
                })
                .finally( (  ) => {
                    this.loading = false
                });
            }
        },
   }
}
</script>

<style>
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
</style>